@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;400&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    background-image: url("/src/images/cristian-palmer-XexawgzYOBc-unsplash-modified.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    font-family: "Noto Sans JP", sans-serif;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

/* Home page styles */

.home header {
    display: flex;
    justify-content: center;
    margin-top: 5rem;
}

.home header h1 {
    color: seashell;
    text-shadow: 0 0 5px rgb(14, 72, 105);
    font-family: "Noto Sans JP", sans-serif;
    font-size: 400;
    font-style: oblique;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: 0.1em;
    -webkit-animation: typing 4s steps(30, end);
    -moz-animation: typing 4s steps(30, end);
    -ms-animation: typing 4s steps(30, end);
    -o-animation: typing 4s steps(30, end);
    animation: typing 4s steps(30, end);
}

.bubbles,
.more-bubbles {
    display: flex;
    margin: 0;
    padding: 0;
    outline: 1px solid transparent;
}

.home a {
    height: 9rem;
    width: 9rem;
    background: transparent;
    border-radius: 50%;
    border: none;
    box-shadow: 
        inset 5px 10px 30px 0.5px rgba(169, 215, 241, 0.5),
        5px 10px 30px 3px rgba(19, 19, 49, 0.5);
    font-family: 'Noto Sans JP', sans-serif;
    font-size: 1em;
    font-weight: bold;
    letter-spacing: 0.03em;
    cursor: pointer;
    transition: 0.5s ease-in-out;
    -webkit-animation: bubbles-motion 10s linear infinite;
    -moz-animation: bubbles-motion 10s linear infinite;
    -ms-animation: bubbles-motion 10s linear infinite;
    -o-animation: bubbles-motion 10s linear infinite;
    animation: bubbles-motion 10s linear infinite;
 }

 .bubbles a,
 .more-bubbles a {
    display: flex;
    justify-content: center;
    align-items: center;
 }
 
 #skillsB {
    margin: 35vh 8vw 0 5vw;
    color: rgb(222, 252, 189);
    text-shadow: 1px 1px 1px rgb(8, 14, 104);
 }

 #projectsB {
    margin: 33vh 7vw 0 17vw;
    text-shadow: 1px 1px 1px rgb(8, 14, 104);
    color: aliceblue;
 }

 #experienceB {
    margin: 36vh 5vw 0 23vw;
    color:rgb(252, 236, 216);
    text-shadow: 1px 1px 1px rgb(8, 14, 104);
 }

 #educationB {
    margin: 0 0 0 20vw;
    color: rgb(109, 233, 250);
    text-shadow: 1px 1px 1px rgb(8, 14, 104);
 }

 #aboutB {
    margin: 0 0 0 30vw;  
    color: rgb(217, 216, 252);
    text-shadow: 1px 1px 1px rgb(8, 14, 104);
 }

.hov a:hover {
    animation: none;
    height: 10rem;
    width: 10rem;
}

#skillsB:hover {
    text-shadow: 1px 1px 1px black;
    box-shadow: 0 0 30px 3px rgb(222, 252, 189);
}

#projectsB:hover {
    text-shadow: 1px 1px 1px black;
    box-shadow: 0 0 30px 3px aliceblue;
}

#experienceB:hover {
    text-shadow: 1px 1px 1px black;
    box-shadow: 0 0 30px 3px rgb(222, 252, 189);
}

#educationB:hover {
    text-shadow: 1px 1px 1px black;
    box-shadow: 0 0 30px 3px rgb(109, 233, 250);
}

#aboutB:hover {
    text-shadow: 1px 1px 1px black;
    box-shadow: 0 0 30px 3px rgb(217, 216, 252);
}

@-webkit-keyframes bubbles-motion {
    0% {
        height: 9rem;
        width: 9rem;
    }

    12.5% {
        height: 9.25rem;
        width: 9.25rem;
    }

    25% {
        height: 9.50rem;
        width: 9.50rem;
    }

    50% {
        height: 10rem;
        width: 10rem;
    }

    75% {
        height: 9.50rem;
        width: 9.50rem;
    }

    87.5% {
        height: 9.25rem;
        width: 9.25rem;
    }

    100% {
        height: 9rem;
        width: 9rem;
    }
}

@keyframes bubbles-motion {
  0% {
    height: 9rem;
    width: 9rem;
  }

  12.5% {
      height: 9.25rem;
      width: 9.25rem;
  }

  25% {
      height: 9.50rem;
      width: 9.50rem;
  }

  50% {
      height: 10rem;
      width: 10rem;
  }

  75% {
      height: 9.50rem;
      width: 9.50rem;
  }

  87.5% {
      height: 9.25rem;
      width: 9.25rem;
  }

  100% {
      height: 9rem;
      width: 9rem;
  }
}

@-webkit-keyframes typing {
    from { width: 0}
    to { width: 100% }
}

@keyframes typing {
    from { width: 0}
    to { width: 100% }
}


/* Navbar styles */

.navbar {
    display: flex;
    justify-content: space-around;
    background-image: linear-gradient(rgb(26, 26, 31) 40%, transparent 60%);
    height: 20vh;
    margin: 0;
    padding: 2vh 0;
}

.name a {
    color: seashell;
}

.name a:hover {
    text-shadow: 1px 1px 1px rgb(172, 221, 228);
}

.mobile-menu {
    display: none;
}

.links a {
    color: rgb(172, 221, 228);
    margin-right: 1vw;
    transition: 0.5s ease-in-out;
}

.links a:hover {
    color: seashell;
    transition: color 0.5s linear;
}

.link-selected {
    color: seashell !important;
    border-bottom: 2px solid seashell;
}

/* Footer styles */
footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 19vh;
    background-image: linear-gradient(transparent 40%, rgb(26, 26, 31) 60%);
}

address {
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    padding-bottom: 1rem;
}

address p {
    margin-right: 1vw;
    color:whitesmoke;
    font-size: 0.9rem;
    font-style: normal;
}

address li {
    margin-right: 14vw;
    font-size: 0.8rem;
    font-style: normal;
    list-style-type: none;
}

address a {
    color: rgb(172, 221, 228);
}

address a:hover {
    color: whitesmoke;
    border-bottom: 1px solid whitesmoke;
}

/* Skills section styles */

.skills {
    display: flex;
    justify-content: center;
}

.wrapper {
    position: relative;
    height: 33vh;
    width: 16vw;
    margin: 17vh 1vw 0 1vw;
}

.wrapper:hover {
    height: 55vh;
    width: 25vw;
    margin-top: 5vh;
}

.background {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 33vh;
    width: 100%;
    border: 15px solid black;
    background-color: black;
}

.text .main-heading {
    position: absolute;
    color: seashell;
    text-shadow: 4px 4px 4px black;
    top: 0;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    justify-content: center;
    font-size: 1.1rem;
}

.coding-details,
.general-details,
.other-details {
    display: none;
}

#coding-main-ul,
.general-details ul {
    margin-top: 3vh;
    margin-left: 1vw;
}

.coding-details li,
.general-details li {
    color: whitesmoke;
    font-size: 0.95rem;
}

#coding-main-ul ul {
    margin: 1vh 2vw;
}

.general-details li {
    margin-bottom: 0.8rem;
}

.other-details h4 {
    margin-top: 1rem;
    margin-bottom: 0.2rem;
}

.other-sublist {
    font-size: 0.9rem;
    padding-left: 2rem;
    color: whitesmoke;
}

.text:hover .coding-details,
.text:hover .general-details {
    position: absolute;
    color: seashell;
    background-color: rgb(8, 8, 36);
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    text-align: left;
    padding: 0 2vw;
}

.text:hover .other-details {
    visibility: visible; 
    position: absolute;
    color: seashell;
    background-color: rgb(8, 8, 36);
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-content: center;
    height: 100%;
    width: 100%;
    text-align: left;
    padding: 3vh 2vw;
}

.text:hover .other-details h3 {
    text-align: center;
}

/* Projects section styles */

.projects-container {
    display: flex;
    flex-direction: column;
    align-content: center;
}

.projects {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 8vh 10vw 2vh 10vw;
}

.projects-wrapper {
    position: relative;
    height: 28vh;
    width: 25vw;
    margin-bottom: 3vh;
 }

.projects-background {
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    height: 28vh;
    width: 100%;
    border: 15px solid black;
    background-color: black;
}

.description {
    display: none;
}

.projects-wrapper:hover .description {
    position: absolute;
    color: seashell;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    text-align: center;
    padding: 0 2vw;
}

.description p {
    margin: 3vh;
    font-size: 0.9rem;
}

.description a {
    text-decoration: none;
    color: rgb(8, 8, 36);
}

.description button {
    background-color: rgb(172, 221, 228);
    color: rgb(8, 8, 36);
    font-weight: bold;
    border-radius: 8px;
    width: 7vw;
    height: 4vh;
    cursor: pointer;
}

.description button:hover {
    background-color:  whitesmoke;
}

/* Resume section styles */

.resume-details {
    color: seashell;
    margin: 0 10vw;
}

.resume-imgs {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
    max-width: 77vw;
}

.resume-details h3 {
    display: block;
    text-align: center;
    margin: 10vh 0;
}

#resume-img-details {
    height: 58vh;
    width: 55vw;
    box-shadow: 5px 10px 30px 0.5px rgba(172, 221, 228, 0.4);
}

#resume-img-mob, 
#resume-mob-details {
    height: 58vh;
    width: 10vw;
    box-shadow: 5px 10px 30px 0.5px rgba(172, 221, 228, 0.4);
}

#resume-img-mob {
    margin: 0 0.5vw;
}

.resume-more-info {
    display: block;
    font-size: 0.9rem;
    color: whitesmoke;
    margin: 10vh 0;
}

.resume-more-info a {
    color: rgb(172, 221, 228);
    font-style: italic;
}

.resume-more-info a:hover {
    color: whitesmoke;
    border-bottom: 1px solid whitesmoke;
}

/* Homepage section styles */

.homepage-details {
    display: block;
    text-align: center;
    color: seashell;
}

.homepage-details h3 {
    margin: 10vh 0;
}

.homepage-details video {
    height: 60vh;
    width: 50vw;
}

.homepage-more-info {
    margin: 10vh 0;
    font-size: 0.9rem;
    color: whitesmoke;
}

.homepage-more-info a {
    color: rgb(172, 221, 228);
    font-style: italic;
}

.homepage-more-info a:hover {
    color: whitesmoke;
    border-bottom: 1px solid whitesmoke;
}

/* Space Stuff section styles */

.space-stuff-details {
    color: seashell;
    margin: 0 10vw;
}

.space-stuff-details h3 {
    display: block;
    text-align: center;
    margin: 10vh 0;
}

.space-stuff-imgs {
    display: grid;
    grid-template-columns: 3fr 1fr;
