    color: whitesmoke;
    margin: 10vh 14vw 10vh 5vw;
}

.space-stuff-more-info a {
    color: rgb(172, 221, 228);
    font-style: italic;
}

.space-stuff-more-info a:hover {
    color: whitesmoke;
    border-bottom: 1px solid whitesmoke;
}

/* Currencies section styles */

.currencies-details {
    color: seashell;
    margin: 0 10vw;
}

.currencies-details h3 {
    display: block;
    text-align: center;
    margin: 10vh 0;
}

.currencies-details-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.currencies-imgs {
    display: flex;
    margin: 10vh auto;
}

#currencies-img-details {
    height: 60vh;
    width: 60vw;
    box-shadow: 5px 10px 30px 0.5px rgba(172, 221, 228, 0.4);
}

.currencies-more-info {
    text-align: justify;
    font-size: 0.9rem;
    color: whitesmoke;
    margin: 10vh auto;
}

.currencies-more-info a {
    color: rgb(172, 221, 228);
    font-style: italic;
}

.currencies-more-info a:hover {
    color: whitesmoke;
    border-bottom: 1px solid whitesmoke;
}

/* Experience section styles */

.experience-container {
    display: flex;
    flex-direction: column;
}

.work {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 1vh 8vw;
}

.experience-wrapper {
    position: relative;
    height: 30vh;
    width: 20vw;
    margin-bottom: 1vh;
}

.work-background {
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    height: 30vh;
    width: 100%;
    border: 15px solid black;
    background-color: black;
}

.job-description {
    display: none;
}

.experience-wrapper:hover .job-description {
    position: absolute;
    color: seashell;
    background-color: rgba(0, 0, 0, 0.75);
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    text-align: center;
    padding: 0 2vw;
}

.job-description h3 {
    font-size: 0.9rem;
    margin-top: 0.8rem;
}

.job-description h4 {
    font-size: 0.8rem;
    margin: 0.8rem 0;
}

.job-description p {
    font-size: 0.8rem;
    color: whitesmoke;
}

.job-description button {
    margin: 1rem 0;
    background-color: rgb(172, 221, 228);
    color: rgb(8, 8, 36);
    font-weight: bold;
    border-radius: 8px;
    width: 7vw;
    height: 4vh;
    cursor: pointer;
}

.job-description button a {
    color: rgb(8, 8, 36);
}

.job-description button:hover {
    background-color:  whitesmoke;
}

/* jobs sections styles */

.job-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: whitesmoke;
    margin: 10vh 10vw;
}

.job-info h3,
.job-info h4 {
    color: seashell;
    text-align: center;
}

.job-info h4 {
    margin-top: 1.5vh;
}

.subtitle {
    font-size: 1.1rem;
}

.job-info ul {
    margin: 10vh 10vw;
}

.smallerUl li {
    margin: 0 10vw;
}

/* Button to top of page styles */

.btn-top,
.btn-top-projects {
    background-color: rgb(172, 221, 228);
    color: rgb(8, 8, 36);
    font-weight: bold;
    border-radius: 8px;
    width: 6vw;
    height: 6vh;
    cursor: pointer;
    margin: 0 0 10vh 35vw;
}

.btn-top {
    margin: 0 0 10vh 35vw;
}

.btn-top-projects {
    margin-bottom: 20vh;
}

#btn-homepage-top {
    margin: 0 0 20vh 0;
}

.btn-top:hover,
.btn-top-projects:hover {
    background-color:  whitesmoke;
}

/* Education section styles */

