    display: flex;
    margin: 14vh 22vw;
}

.edu-wrapper {
    position: relative;
    height: 35vh;
    width: 26vw;
    margin: 0 0.5vw;
}

.edu-background {
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    height: 35vh;
    width: 100%;
    border: 15px solid black;
    background-color: black;
}

.edu-main-heading {
    position: absolute;
    color: seashell;
    top: 0;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    justify-content: center;
}

.edu-details {
    display: none;
}

.edu-wrapper:hover .edu-main-heading {
    display: none;
}

.edu-wrapper:hover .edu-details {
    position: absolute;
    color: seashell;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    text-align: justify;
    padding: 0 2.5vw;
}

.edu-details h3 {
    padding: 0.5rem 0 1rem 0;
}

#coding-heading {
    padding-bottom: 2rem;
}

.edu-details li {
    color: whitesmoke;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

/* About section styles */
.about {
    display: flex;
}

.photo img {
    height: 150px;
    width: 110px;
    margin-right: 5vw;
    margin-top: -10vh;
    margin-left: 14.7vw;
}

.about-wrapper {
