    border-radius: 50%;
    box-shadow: 
        inset 5px 10px 30px 0.5px rgba(169, 215, 241, 0.5),
        5px 10px 30px 3px rgba(19, 19, 49, 0.5);
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}

.about-info {
    position: absolute;
    color: whitesmoke;
    text-shadow: 0.5px 0.5px 0.5px rgb(8, 8, 36);
    font-size: 0.9rem;
    font-weight: bold;
    line-height: 1.6rem;
    text-align: center;
    margin-top: 9vh;
}

#smallBubble1 {
    height: 24px;
    width: 24px;
    background: transparent;
    border-radius: 50%;
    box-shadow: 
        inset 2px 3px 10px 0.5px rgba(169, 215, 241, 0.5),
        5px 10px 30px 3px rgba(19, 19, 49, 0.5);
    margin: 6vh 0 0 -4vw;
}

#smallBubble2 {
    height: 32px;
    width: 32px;
    background: transparent;
    border-radius: 50%;
    box-shadow: 
        inset 2px 3px 10px 0.5px rgba(169, 215, 241, 0.5),
        5px 10px 30px 3px rgba(19, 19, 49, 0.5);
    margin: 8vh 0 0 1vw;
}

#smallBubble3 {
    height: 45px;
    width: 45px;
    background: transparent;
    border-radius: 50%;
    box-shadow: 
        inset 2px 3px 10px 0.5px rgba(169, 215, 241, 0.5),
        5px 10px 30px 3px rgba(19, 19, 49, 0.5);
    margin: 10vh 0 0 1vw;
}

/* NotFound section styles */

.not-found {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 24vh;
    color: seashell;
}

.not-found h3 {
    margin-bottom: 1rem;
    font-size: 1.3rem;
}

.not-found p {
    color: white;
    font-size: 1.1rem;
}

/* Responsive styles */

@media screen and (max-width: 1295px) {
    body {
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
    }

    /* Home page styles */
    .home {
        padding-bottom: 5rem;
    }

    .home header {
        margin: 3rem 0;
    }

    .home header h1 {
        font-size: 0.8rem;
        -webkit-animation: typing 2.5s steps(30, end);
        -moz-animation: typing 2.5s steps(30, end);
        -ms-animation: typing 2.5s steps(30, end);
        -o-animation: typing 2.5s steps(30, end);
        animation: typing 2.5s steps(30, end);
    }

    .bubbles,
    .more-bubbles {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .bubble-bullet {
        display: flex;
        height: 2rem;
        width: 2rem;
        background-color: transparent; 
        border-radius: 50%;
        box-shadow: 
            inset 2px 3px 10px 0.5px rgba(169, 215, 241, 0.5),
            5px 10px 30px 3px rgba(19, 19, 49, 0.5);
        -webkit-animation: bubbles-motion 20s linear infinite;
        -moz-animation: bubbles-motion 20s linear infinite;
        -ms-animation: bubbles-motion 20s linear infinite;
        -o-animation: bubbles-motion 20s linear infinite;
        animation: bubbles-motion 20s linear infinite;
    }

    .home a {
        height: 6.5rem;
        width: 6.5rem;
        box-shadow: 
            inset 2px 3px 10px 0.5px rgba(169, 215, 241, 0.5),
            5px 10px 30px 3px rgba(19, 19, 49, 0.5);
        font-size: 0.7rem;
        -webkit-animation: bubbles-motion 20s linear infinite;
        -moz-animation: bubbles-motion 20s linear infinite;
        -ms-animation: bubbles-motion 20s linear infinite;
        -o-animation: bubbles-motion 20s linear infinite;
        animation: bubbles-motion 20s linear infinite;
     }

    .bubbles a,
    .more-bubbles a {
        text-align: center;
    }
 
    #skillsB,
    #projectsB,
    #experienceB,
    #educationB,
    #aboutB {
        margin: 2.5rem;
    }

    .hov a:hover {
        animation: none;
        height: 7rem;
        width: 7rem;
    }

    @-webkit-keyframes bubbles-motion {
        0% {
            transform: translateY(0vh);
        }

        12.5% {
            transform: translateY(1vh);
        }

        25% {
            transform: translateY(2vh);
        }

        50% {
            transform: translateY(3vh);
        }

        75% {
            transform: translateY(-2vh);
        }

        87.5% {
            transform: translateY(-1vh);
        }

        100% {
            transform: translateY(0vh);
        }
    }

    @keyframes bubbles-motion {
        0% {
            transform: translateY(0vh);
        }

        12.5% {
            transform: translateY(1vh);
        }

        25% {
            transform: translateY(2vh);
        }

        50% {
            transform: translateY(3vh);
        }

        75% {
            transform: translateY(-2vh);
        }

        87.5% {
            transform: translateY(-1vh);
        }

        100% {
            transform: translateY(0vh);
        }
    }

